body {
  background-color: #2b2b2b;
  color: #ffffff;
  font-family: Arial, sans-serif;
}

.app-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

input {
  padding: 10px;
  margin-right: 10px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}

button {
  padding: 10px 15px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

li button {
  margin-left: 10px;
  background-color: #f44336;
}

li button:hover {
  background-color: #d32f2f;
}
